<template xmlns="http://www.w3.org/1999/html">
  <div class="app-container">
    <div class="filter-container">
      <el-form :inline="true">
        <el-form-item>
          <el-button
            type="success"
            size="mini"
            icon="el-icon-refresh"
            v-if="hasPermission('api:list')"
            @click.native.prevent="getapiList"
          >
          </el-button>
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-plus"
            v-if="hasPermission('api:add')"
            @click.native.prevent="showAddapiDialog"
          >API
          </el-button>
          <el-button
            type="primary"
            size="mini"
            v-if="hasPermission('api:add')"
            @click.native.prevent="showCopyapiDialog"
          >API
          </el-button>
          <el-button
            type="primary"
            size="mini"
            v-if="hasPermission('api:add')"
            @click.native.prevent="showPostManDialog"
          >PostMan
          </el-button>
          <el-button
            type="primary"
            size="mini"
            v-if="hasPermission('api:add')"
            @click.native.prevent="showSwaggerDialog"
          >Swagger
          </el-button>
          <el-button
            type="danger"
            size="mini"
            v-if="hasPermission('api:add')"
            @click.native.prevent="removebatchapi"
          >
          </el-button>
        </el-form-item>

        <span v-if="hasPermission('api:search')">
          <el-form-item>
            <el-input style="width: 130px" v-model="search.apiname" clearable @keyup.enter.native="searchBy" placeholder="API"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input style="width: 130px" v-model="search.deployunitname" clearable @keyup.enter.native="searchBy" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-input style="width: 130px" v-model="search.modelname" clearable @keyup.enter.native="searchBy" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="searchBy" :loading="btnLoading"></el-button>
          </el-form-item>

        </span>


      </el-form>
    </div>
    <el-table
      :data="apiList"
      :key="itemKey"
      @selection-change="handleSelectionChange"
      v-loading.body="listLoading"
      element-loading-text="loading"
      border
      fit
      highlight-current-row
    >
      <el-table-column
        type="selection"
        width="40">
      </el-table-column>
      <el-table-column label="" align="center" width="45">
        <template slot-scope="scope">
          <span v-text="getIndex(scope.$index)"></span>
        </template>
      </el-table-column>
      <el-table-column label="API" align="center" prop="apiname" width="120"/>
      <el-table-column label="" align="center" prop="deployunitname" width="130"/>
      <el-table-column label="" align="center" prop="modelname" width="80"/>
      <el-table-column label="" align="center" prop="apistyle" width="80"/>
      <el-table-column label="" align="center" prop="visittype" width="80"/>
      <el-table-column label="" align="center" prop="path" width="60">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>{{ scope.row.path }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">...</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" prop="requestcontenttype" width="80"/>
      <el-table-column label="" align="center" prop="casecounts" width="60"/>
      <el-table-column label="" align="center" prop="creator" width="60"/>
      <el-table-column label="" align="center" prop="createTime" width="140">
        <template slot-scope="scope">{{ unix2CurrentTime(scope.row.createTime) }}</template>
      </el-table-column>
      <el-table-column label="" align="center" prop="lastmodifyTime" width="140">
        <template slot-scope="scope">{{ unix2CurrentTime(scope.row.lastmodifyTime) }}
        </template>
      </el-table-column>

      <el-table-column label="" align="center" width="240"
                       v-if="hasPermission('api:update')  || hasPermission('api:delete')">
        <template slot-scope="scope">
          <el-button
            type="warning"
            size="mini"
            v-if="hasPermission('api:update') && scope.row.id !== id"
            @click.native.prevent="showUpdateapiDialog(scope.$index)"
          >
          </el-button>
          <el-button
            type="danger"
            size="mini"
            v-if="hasPermission('api:delete') && scope.row.id !== id"
            @click.native.prevent="removeapi(scope.$index)"
          >
          </el-button>
          <!--          <el-button-->
          <!--            type="primary"-->
          <!--            size="mini"-->
          <!--            v-if="hasPermission('api:delete') && scope.row.id !== id"-->
          <!--            @click.native.prevent="ShowParamsDialog(scope.$index)"-->
          <!--          >API-->
          <!--          </el-button>-->
          <el-button
            type="primary"
            size="mini"
            v-if="hasPermission('api:delete') && scope.row.id !== id"
            @click.native.prevent="ShowNewParamsDialog(scope.$index)"
          >API
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="search.page"
      :page-size="search.size"
      :total="total"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
    ></el-pagination>
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <el-form
        status-icon
        class="small-space"
        label-position="left"
        label-width="120px"
        style="width: 450px; margin-left:50px;"
        :model="tmpapi"
        ref="tmpapi"
      >
        <el-form-item label="API:" prop="apiname" required>
          <el-input
            placeholder="API"
            type="text"
            maxlength="40"
            prefix-icon="el-icon-edit"
            auto-complete="off"
            v-model="tmpapi.apiname"
          />
        </el-form-item>
        <el-form-item label=":" prop="visittype" required>
          <el-select v-model="tmpapi.visittype" placeholder="" style="width:100%"
                     @change="visitypeselectChanged($event)">
            <el-option label="" value="''" style="display: none"/>
            <div v-for="(vistype, index) in visittypeList" :key="index">
              <el-option :label="vistype.dicitmevalue" :value="vistype.dicitmevalue" required/>
            </div>
          </el-select>
        </el-form-item>
        <el-form-item label="API:" prop="apistyle" required>
          <el-select v-model="tmpapi.apistyle" placeholder="api" style="width:100%">
            <el-option label="" value=""></el-option>
            <el-option label="Restful" value="Restful"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Url:" prop="path" >
          <el-input
            type="text"
            placeholder="IP/user/login"
            maxlength="200"
            prefix-icon="el-icon-message"
            auto-complete="off"
            v-model.trim="tmpapi.path"
          />
        </el-form-item>
        <el-form-item label=":" prop="deployunitname" required>
          <el-select v-model="tmpapi.deployunitname" placeholder="" style="width:100%"
                     @change="selectChanged($event)">
            <el-option label="" value="''" style="display: none"/>
            <div v-for="(depunitname, index) in deployunitList" :key="index">
              <el-option :label="depunitname.deployunitname" :value="depunitname.deployunitname" required/>
            </div>
          </el-select>
        </el-form-item>

        <el-form-item label=":" prop="modelname" >
          <el-select v-model="tmpapi.modelname" placeholder="" style="width:100%"
                     @change="modelselectChanged($event)">
            <el-option label="" value="''" style="display: none"/>
            <div v-for="(model, index) in modelList" :key="index">
              <el-option :label="model.modelname" :value="model.modelname" required/>
            </div>
          </el-select>
        </el-form-item>

        <!--        <el-form-item label=":" prop="requesttype" required >-->
        <!--          <el-select v-model="tmpapi.requesttype" placeholder="" style="width:100%">-->
        <!--            <el-option label="Url" value="Url"></el-option>-->
        <!--            <el-option label="Body" value="Body"></el-option>-->
        <!--            <el-option label="Both" value="Both"></el-option>-->
        <!--          </el-select>-->
        <!--        </el-form-item>-->

        <div v-if="requestcontenttypeVisible">
          <el-form-item label=":" prop="requestcontenttype" required>
            <el-select v-model="tmpapi.requestcontenttype" placeholder="" style="width:100%">
              <el-option label="" value="''" style="display: none"/>
              <div v-for="(type, index) in requestcontentList" :key="index">
                <el-option :label="type.dicitmevalue" :value="type.dicitmevalue" required/>
              </div>
            </el-select>
          </el-form-item>
        </div>

        <!--        <el-form-item label=":" prop="responecontenttype" required>-->
        <!--          <el-select v-model="tmpapi.responecontenttype" placeholder="" style="width:100%">-->
        <!--            <el-option label="" value="''" style="display: none" />-->
        <!--            <div v-for="(type, index) in responecontenttypeList" :key="index">-->
        <!--              <el-option :label="type.dicitmevalue" :value="type.dicitmevalue" required/>-->
        <!--            </div>-->
        <!--          </el-select>-->
        <!--        </el-form-item>-->

        <el-form-item label=":" prop="memo">
          <el-input
            type="text"
            maxlength="100"
            prefix-icon="el-icon-message"
            auto-complete="off"
            v-model="tmpapi.memo"
          />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native.prevent="dialogFormVisible = false"></el-button>
        <el-button
          type="danger"
          v-if="dialogStatus === 'add'"
          @click.native.prevent="$refs['tmpapi'].resetFields()"
        >
        </el-button>
        <el-button
          type="success"
          v-if="dialogStatus === 'add'"
          :loading="btnLoading"
          @click.native.prevent="addapi"
        >
        </el-button>
        <el-button
          type="success"
          v-if="dialogStatus === 'update'"
          :loading="btnLoading"
          @click.native.prevent="updateapi"
        >
        </el-button>
      </div>
    </el-dialog>
    <el-dialog :title="CopyApi" :visible.sync="CopydialogFormVisible">
      <el-form
        status-icon
        class="small-space"
        label-position="left"
        label-width="120px"
        style="width: 400px; margin-left:50px;"
        :model="tmpcopyapi"
        ref="tmpcopyapi"
      >
        <el-form-item label="" prop="sourcedeployunitname" required>
          <el-select v-model="tmpcopyapi.sourcedeployunitname" placeholder="" style="width:100%"
                     @change="CopyAPISourceDeployselectChanged($event)">
            <el-option label="" value="''" style="display: none"/>
            <div v-for="(depunitname, index) in deployunitList" :key="index">
              <el-option :label="depunitname.deployunitname" :value="depunitname.deployunitname" required/>
            </div>
          </el-select>
        </el-form-item>

        <el-form-item label="API" prop="sourceapiname" required>
          <el-select v-model="tmpcopyapi.sourceapiname" placeholder="api" style="width:100%"
                     @change="CopySourceAPIChanged($event)">
            <el-option label="" value="''" style="display: none"/>
            <div v-for="(testapi, index) in sourceapiList" :key="index">
              <el-option :label="testapi.apiname" :value="testapi.apiname" required/>
            </div>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="objectdeployunitname" required>
          <el-select v-model="tmpcopyapi.objectdeployunitname" placeholder="" style="width:100%"
                     @change="CopyObjectAPIDeployUnitChanged($event)">
            <el-option label="" value="''" style="display: none"/>
            <div v-for="(depunitname, index) in deployunitList" :key="index">
              <el-option :label="depunitname.deployunitname" :value="depunitname.deployunitname" required/>
            </div>
          </el-select>
        </el-form-item>

        <el-form-item label="API" prop="newapiname" required>
          <el-input
            type="text"
            maxlength="40"
            prefix-icon="el-icon-edit"
            auto-complete="off"
            v-model="tmpcopyapi.newapiname"
          />
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native.prevent="CopydialogFormVisible = false"></el-button>
        <el-button
          type="success"
          :loading="btnLoading"
          @click.native.prevent="copyapi"
        >
        </el-button>
      </div>
    </el-dialog>
    <el-dialog title='API' :visible.sync="ParamsdialogFormVisible">
      <div class="filter-container">
        <el-form :inline="true">
          <el-form-item>
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-plus"
              v-if="hasPermission('apiparams:add')"
              @click.native.prevent="showAddapiparamsDialog"
            >API
            </el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-table
        :data="apiparamsList"
        :key="itemKey"
        v-loading.body="listLoading"
        element-loading-text="loading"
        border
        fit
        highlight-current-row
      >
        <el-table-column label="" align="center" width="45">
          <template slot-scope="scope">
            <span v-text="paramgetIndex(scope.$index)"></span>
          </template>
        </el-table-column>
        <el-table-column label="API" align="center" prop="apiname" width="120"/>
        <el-table-column label="" align="center" prop="propertytype" width="80"/>
        <el-table-column label="" align="center" prop="bodytype" width="80"/>
        <el-table-column label="" align="center" prop="deployunitname" width="130"/>
        <el-table-column label="" align="center" prop="keynamebak" width="100">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>{{ scope.row.keynamebak }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">...</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="" align="center"
                         v-if="hasPermission('apiparams:update')  || hasPermission('apiparams:delete')">
          <template slot-scope="scope">
            <el-button
              type="warning"
              size="mini"
              v-if="hasPermission('apiparams:update') && scope.row.id !== id"
              @click.native.prevent="showUpdateapiparamsDialog(scope.$index)"
            >
            </el-button>
            <el-button
              type="danger"
              size="mini"
              v-if="hasPermission('apiparams:delete') && scope.row.id !== id"
              @click.native.prevent="removeapiparams(scope.$index)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog  title='API' :visible.sync="NewParamsdialogFormVisible">
      <div class="filter-container">
        <el-form :inline="true">
          <template>
            <el-tabs v-model="activeName" @tab-click="tabclick" type="card" ref="tabs">
              <el-tab-pane label="Header" name="zero">
                <template>
                  <el-table :data="Headertabledatas" border @selection-change="handleSelectionChange">
                    <el-table-column label="" prop="keyname" align="center">
                      <template slot-scope="scope">
                        <el-input size="mini" placeholder="" v-model="scope.row.keyname"></el-input>
                      </template>
                    </el-table-column>
                    <el-table-column label="" prop="keydefaultvalue" align="center">
                      <template slot-scope="scope">
                        <el-input size="mini" placeholder="" v-model="scope.row.keydefaultvalue"></el-input>
                      </template>
                    </el-table-column>
                    <el-table-column label="" align="center">
                      <template slot-scope="scope">
                        <el-button type="primary" size="mini" @click="copeHeader(scope.row,scope.$index)">
                        </el-button>
                        <el-button type="primary" size="mini" @click="delectHeader(scope.$index)"></el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </template>
              </el-tab-pane>
              <el-tab-pane label="Params" name="first">
                <template>
                  <el-table :data="Paramstabledatas" border @selection-change="handleSelectionChange">
                    <el-table-column label="" prop="keyname" align="center">
                      <template slot-scope="scope">
                        <el-input size="mini" placeholder="" v-model="scope.row.keyname"></el-input>
                      </template>
                    </el-table-column>
                    <el-table-column label="" prop="keytype" align="center">
                      <template slot-scope="scope">
                        <el-select v-model="scope.row.keytype" style="width:100%" placeholder="">
                          <el-option label="Number" value="Number"></el-option>
                          <el-option label="String" value="String"></el-option>
                          <el-option label="Array" value="Array"></el-option>
                        </el-select>
                      </template>
                    </el-table-column>
                    <el-table-column label="" prop="keydefaultvalue" align="center">
                      <template slot-scope="scope">
                        <el-input size="mini" placeholder="" v-model="scope.row.keydefaultvalue"></el-input>
                      </template>
                    </el-table-column>
                    <el-table-column label="" align="center">
                      <template slot-scope="scope">
                        <el-button type="primary" size="mini" @click="copeParam(scope.row,scope.$index)">
                        </el-button>
                        <el-button type="primary" size="mini" @click="delectParam(scope.$index)"></el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </template>
              </el-tab-pane>
              <el-tab-pane label="Body" name="second">
                <template>
                  <div v-if="BodyParamDataVisible" class="components-container">
                    <el-table :data="Bodytabledatas" border>
                      <el-table-column label="" prop="keyname" align="center">
                        <template slot-scope="scope">
                          <el-input size="mini" placeholder="" v-model="scope.row.keyname"></el-input>
                        </template>
                      </el-table-column>
                      <el-table-column label="" prop="keytype" align="center">
                        <template slot-scope="scope">
                          <el-select v-model="scope.row.keytype" style="width:100%" placeholder="">
                            <el-option label="Number" value="Number"></el-option>
                            <el-option label="String" value="String"></el-option>
                            <el-option label="Array" value="Array"></el-option>
                          </el-select>
                        </template>
                      </el-table-column>
                      <el-table-column label="" prop="keydefaultvalue" align="center">
                        <template slot-scope="scope">
                          <el-input size="mini" placeholder="" v-model="scope.row.keydefaultvalue"></el-input>
                        </template>
                      </el-table-column>
                      <el-table-column label="" align="center">
                        <template slot-scope="scope">
                          <el-button type="primary" size="mini" @click="copeBodyParam(scope.row,scope.$index)">
                          </el-button>
                          <el-button type="primary" size="mini" @click="delectBodyParam(scope.$index)"></el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div >
                  <div v-if="BodyDataVisible" >
                    <el-form
                      :model="tmpapiparams"
                      ref="tmpapiparams">
                      <el-form-item  prop="keyname">
<!--                        <div class="editor-container">-->
<!--                          <json-editor ref="jsonEditor" v-model="tmpapiparams.keyname" />-->
<!--                        </div>-->


                        <el-input
                            type="textarea"
                          style="width: 650px;height: 400px;color: #0000FF"
                          rows="20" cols="70"
                          prefix-icon="el-icon-message"
                          auto-complete="off"
                          v-model="tmpapiparams.keyname"
                          :placeholder="paramsplaceholder"
                        />
                      </el-form-item>
                    </el-form>
                  </div>
                </template>
              </el-tab-pane>
            </el-tabs>
          </template>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native.prevent="NewParamsdialogFormVisible = false"></el-button>
        <el-button
          type="success"
          :loading="btnLoading"
          @click.native.prevent="addapiallparams"
        >
        </el-button>
      </div>
    </el-dialog>
    <el-dialog :title="paramstextMap[ParamsdialogStatus]" :visible.sync="ModifydialogFormVisible">
      <el-form
        status-icon
        class="small-space"
        label-position="left"
        label-width="120px"
        style="width: 600px; margin-left:30px;"
        :model="tmpapiparams"
        ref="tmpapiparams"
      >
        <el-form-item label="" prop="propertytype" required>
          <el-select v-model="tmpapiparams.propertytype" placeholder="" style="width:100%"
                     @change="paratypeselectChanged($event)">
            <el-option label="" value="''" style="display: none"/>
            <div v-for="(para, index) in paramlist" :key="index">
              <el-option :label="para.value" :value="para.value"/>
            </div>
          </el-select>
        </el-form-item>

        <div v-if="BodyVisible">
          <el-form-item label="" prop="bodytype" required>
            <el-select v-model="tmpapiparams.bodytype" style="width:100%" placeholder=""
                       @change="submittypeselectChanged($event)">
              <el-option label="" value=""></el-option>
              <el-option label="Json" value="Json"></el-option>
              <el-option label="Xml" value="Xml"></el-option>
            </el-select>
          </el-form-item>
        </div>


        <el-form-item label="" prop="keyname" required>
          <el-input
            type="textarea"
            rows="20" cols="50"
            prefix-icon="el-icon-message"
            auto-complete="off"
            v-model.trim="tmpapiparams.keyname"
            :placeholder="paramsplaceholder"
          />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native.prevent="ModifydialogFormVisible = false"></el-button>
        <el-button
          type="success"
          v-if="ParamsdialogStatus === 'add'"
          :loading="btnLoading"
          @click.native.prevent="addapiparams"
        >
        </el-button>
        <el-button
          type="success"
          v-if="ParamsdialogStatus === 'update'"
          :loading="btnLoading"
          @click.native.prevent="updateapiparams"
        >
        </el-button>
      </div>
    </el-dialog>
    <el-dialog title='PostMan(V2.1)' :visible.sync="dialogAddFile">
      <el-form
        :model="uploadData"
        ref="uploadData"
      >
        <el-form-item label=":" prop="deptname" required>
          <el-select v-model="uploadData.deptname" placeholder="" style="width:100%"
                     @change="uploadselectChanged($event)">
            <el-option label="" value="''" style="display: none"/>
            <div v-for="(depunitname, index) in deployunitList" :key="index">
              <el-option :label="depunitname.deployunitname" :value="depunitname.deployunitname" required/>
            </div>
          </el-select>
        </el-form-item>

        <el-form-item label="API:" prop="apistyle" required>
          <el-select v-model="uploadData.apistyle" placeholder="api" style="width:100%">
            <el-option label="" value=""></el-option>
            <el-option label="Restful" value="restful"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-upload
            style="display: inline"
            :auto-upload="false"
            :on-change="handleChange"
            :on-remove="removehandleChange"
            :file-list="this.fileList"
            accept=".json"
            limit="1"
            action="#">
            <el-button  type="success"></el-button>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button  type="success" @click="upload"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
